import React from "react";
import Row from "muicss/lib/react/row";
import Panel from "muicss/lib/react/panel";
import Col from "muicss/lib/react/col";
import Deal from "./individualDeal";
import ClientModal from "../common/ClientModal";

class dealQuotes extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cardShowing: false,
      modalOpen: false,
      contact: {},
      brokerName: "",
      brokerId: "",
    };
    this.openContactModal = this.openContactModal.bind(this);
  }

  componentDidMount() {
    if (
      sessionStorage.getItem("mq_broker_active_banker_id") ===
      this.props.deal.deal_id
    ) {
      this.setState({ cardShowing: true });
    }
  }

  openChildDeals() {
    this.setState({ cardShowing: !this.state.cardShowing });
  }

  openBrokerModal(broker) {
    this.setState({ brokerId: broker.broker_id }, function () {
      this.openContactModal(this.state.brokerId, broker.broker_name);
    });
  }

  openContactModal(contactId, contactName) {
    this.props.getContactFullInfo(contactId);
    this.setState({
      modalOpen: true,
      brokerName: contactName,
      brokerId: contactId,
    });
  }

  closeModal() {
    this.setState({
      modalOpen: false,
    });
  }

  routeToMissingQuotesByBroker(broker) {
    this.props.routeToMissingQuotesByBroker(
      broker.broker_id,
      broker.broker_name
    );
  }

  render() {
    const deals = this.props.deal.my_quotes.map((item, index) => (
      <Deal
        key={index}
        item={item}
        individualIndex={index}
        index={this.props.index}
        deal_id={this.props.deal.deal_id}
        isSnoozed={item.missing_quotes_snooze}
        quoteDeal={this.props.quoteDeal}
        passDeal={this.props.passDeal}
        snoozeDeal={this.props.snoozeDeal}
        irrelevantDeal={this.props.irrelevantDeal}
        openContactModal={this.openContactModal}
        authentication={this.props.authentication}
      />
    ));

    let broker =
      this.props.deal.broker_data &&
      this.props.deal.broker_data.find(
        (b) => b.broker_of_record === 1 || b.is_originator === 1
      );

    return (
      <div className="mq-broker-page">
        <Panel className="panel border-left-panel">
          <Row className="mq-broker-deal-row">
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-deal-col"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.deal.deal_id,
                this.props.deal.deal_id
              )}
            >
              <p className="header-text">{this.props.deal.deal_name}</p>
              <p className="subtitle-text subtitle-text-dark">
                {this.props.deal.city_state}
              </p>
            </Col>
            <Col md="2" sm="4" xs="4" className="mq-broker-deal-col">
              <p
                className="header-text orange-hover"
                onClick={() => this.openBrokerModal(broker)}
              >
                {broker && broker.broker_name}
              </p>
              {broker &&
              (this.props.authentication.is_qts === "true" ||
                this.props.authentication.member_role === 2 ||
                this.props.authentication.member_role === 3) ? (
                <div
                  className="mq-broker-icon-container"
                  onClick={this.routeToMissingQuotesByBroker.bind(this, broker)}
                >
                  <img
                    src="/images/nav-icons/missing-quotes.png"
                    alt="missing-quotes"
                    className="mq-broker-icon"
                  />
                  <img
                    src="/images/icons/missing-quotes3.png"
                    alt="missing-quotes"
                    className="mq-broker-icon-hover"
                  />
                </div>
              ) : (
                ""
              )}
              <p className="subtitle-text">Broker</p>
            </Col>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-deal-col"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.deal.deal_id,
                this.props.deal.deal_id
              )}
            >
              <p className="header-text">{this.props.deal.property_type}</p>
              <p className="subtitle-text">Property Type</p>
            </Col>
            <Col md="2" sm="4" xs="4" className="mq-broker-deal-col">
              <p
                className="header-text orange-hover"
                onClick={this.openContactModal.bind(
                  null,
                  this.props.deal.contact_id,
                  this.props.deal.deal_contact_name
                )}
              >
                {this.props.deal.deal_contact_name}
              </p>
              <p className="subtitle-text">Client</p>
            </Col>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-deal-col"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.deal.deal_id,
                this.props.deal.deal_id
              )}
            >
              <p className="header-text">{this.props.deal.deal_rating}</p>
              <p className="subtitle-text">Forecast</p>
            </Col>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="text-right mq-deal-column-right"
            >
              <div
                className={
                  this.props.authentication.is_qts ? "mq-deal-star" : "hidden"
                }
                onClick={() => {
                  this.props.updateQTSStar(
                    this.props.deal.deal_id,
                    this.props.deal.qts_starred
                  );
                }}
              >
                <p>
                  {this.props.deal.qts_starred === 1 ? (
                    <span className="bank-quote-star">
                      <i className="material-icons orange-text">star</i>
                    </span>
                  ) : (
                    <span className="bank-quote-star">
                      <i className="material-icons">star</i>
                    </span>
                  )}
                </p>
              </div>
              <div className="mq-deal-amount">
                {this.props.deal.quote_count}
              </div>
              <i
                className="material-icons arrow-down"
                onClick={this.openChildDeals.bind(this)}
              >
                {this.state.cardShowing
                  ? "keyboard_arrow_up"
                  : "keyboard_arrow_down"}
              </i>
            </Col>
          </Row>
        </Panel>
        <div
          className={this.state.cardShowing ? "hidden" : "after-panel"}
        ></div>
        <div className={this.state.cardShowing ? "" : "hidden"}>{deals}</div>
        <ClientModal
          isOpen={this.state.modalOpen}
          onRequestClose={this.closeModal.bind(this)}
          onAfterOpen={() => ({})}
          name={this.state.brokerName}
          loading={this.props.loadingContact}
          contact={this.props.contact}
          noContact={this.props.noContact}
        />
      </div>
    );
  }
}

export default dealQuotes;
