import React from "react";
import Row from "muicss/lib/react/row";
import Panel from "muicss/lib/react/panel";
import Col from "muicss/lib/react/col";
import moment from "moment";

class individualBank extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      quoteFormShowing: false,
      quote_id: this.props.item.quote_id,
      modalOpen: false,
      contact: {},
      brokerName: "",
      brokerId: "",
    };
  }

  closeForm() {
    this.setState({ quoteFormShowing: false });
  }

  routeToMissingQuotesByBroker(brokerId, brokerName) {
    this.props.routeToMissingQuotesByBroker(brokerId, brokerName);
  }

  render() {
    let lastNote = this.props.item.all_notes
      ? this.props.item.all_notes[0]
      : null;
    if(this.props.item.quote_status === 7 )return null;
    return (
      <div>
        <Panel key={this.props.index} className="panel">
          <Row
            className={
              this.props.item.non_qts === 1 ||
              this.props.item.quote_status === 7
                ? ""
                : "hidden"
            }
          >
            <p className={this.props.item.non_qts === 1 ? "non-qts" : "hidden"}>
              Non QTS
            </p>

            <span
              className={
                this.props.item.quote_status === 7 ? "deal-tag" : "hidden"
              }
              style={{ background: "#F2BC2B" }}
            >
              Waiting on EU
            </span>
          </Row>
          <Row>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-bank-col"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.item.deal_id,
                this.props.banker_id
              )}
            >
              <p className="header-text">{this.props.item.deal_name}</p>
              <p className="subtitle-text subtitle-text-dark">
                {this.props.item.city_state}
              </p>
            </Col>
            <Col md="2" sm="4" xs="4" className="mq-broker-bank-col">
              <p
                onClick={this.props.openContactModal.bind(
                  null,
                  this.props.brokerId,
                  this.props.broker
                )}
                className="header-text orange-hover"
              >
                {this.props.broker}
              </p>

              {this.props.broker &&
              (this.props.authentication.is_qts === "true" ||
                this.props.authentication.member_role === 2 ||
                this.props.authentication.member_role === 3) ? (
                <div
                  className="mq-broker-icon-container"
                  onClick={this.routeToMissingQuotesByBroker.bind(
                    this,
                    this.props.brokerIdForMQ,
                    this.props.broker
                  )}
                >
                  <img
                    src="/images/nav-icons/missing-quotes.png"
                    alt="missing-quotes"
                    className="mq-broker-icon"
                  />
                  <img
                    src="/images/icons/missing-quotes3.png"
                    alt="missing-quotes"
                    className="mq-broker-icon-hover"
                  />
                  <span className="mq-broker-tooltip">
                    View All Broker's Quotes
                  </span>
                </div>
              ) : (
                ""
              )}

              <p className="subtitle-text">Broker</p>
            </Col>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-bank-col"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.item.deal_id,
                this.props.banker_id
              )}
            >
              <p className="header-text">{this.props.item.requested_amount}</p>
              <p className="subtitle-text">Loan Amount</p>
            </Col>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-bank-col"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.item.deal_id,
                this.props.banker_id
              )}
            >
              <p className="header-text">{this.props.item.property_type}</p>
              <p className="subtitle-text">Property Type</p>
            </Col>
            <Col
              md="2"
              sm="4"
              xs="4"
              className="mq-broker-bank-col"
              onClick={this.props.openContactModal.bind(
                null,
                this.props.item.contact_id,
                this.props.item.deal_contact_name
              )}
            >
              <p className="header-text orange-hover">
                {this.props.item.deal_contact_name}
              </p>
              <p className="subtitle-text">Client</p>
            </Col>
            <Col md="2" sm="4" xs="4" className="mq-broker-bank-col">
              <div
                onClick={this.props.openDealDetails.bind(
                  null,
                  this.props.item.deal_id,
                  this.props.banker_id
                )}
              >
                <p className="header-text">{this.props.item.days_elapsed}</p>
                <p className="subtitle-text">Days Elapsed</p>
              </div>
              <div
                className={
                  this.props.authentication.is_qts ? "bank-star" : "hidden"
                }
                onClick={() => {
                  this.props.updateQTSStar(
                    this.props.item.deal_id,
                    this.props.item.banker_id,
                    this.props.item.quote_id,
                    this.props.item.qts_starred
                  );
                }}
              >
                <p>
                  {this.props.item.qts_starred === 1 ? (
                    <span className="bank-quote-star">
                      <i className="material-icons orange-text">star</i>
                    </span>
                  ) : (
                    <span className="bank-quote-star">
                      <i className="material-icons accent-text">star</i>
                    </span>
                  )}
                </p>
              </div>
            </Col>
          </Row>
          <Row className="empty-row"></Row>
          <Row>
            <Col
              md="2"
              sm="4"
              xs="4"
              onClick={this.props.openDealDetails.bind(
                null,
                this.props.item.deal_id,
                this.props.banker_id
              )}
            >
              <p className="header-text">{this.props.item.deal_rating}</p>
              <p className="subtitle-text">Forecast</p>
            </Col>
            <Col md="8" sm="6" xs="6">
              <p className="header-text padding-left inline-block">
                {lastNote ? lastNote.notes : "--"}
              </p>
              <p className="subtitle-text padding-left">
                Last Note{" "}
                {lastNote ? (
                  <i>
                    by {lastNote.note_by} on{" "}
                    {moment(lastNote.created_at).format("L LT")}
                  </i>
                ) : (
                  ""
                )}
              </p>
            </Col>
          </Row>
          <Row className="deal-options">
            <div
              className={
                this.props.isSnoozed === 1
                  ? "deal-options-left"
                  : "deal-options-left orange-text"
              }
            >
              <p
                onClick={this.props.quoteDeal.bind(
                  null,
                  this.props.item.quote_id,
                  this.props.banker_id
                )}
              >
                Quote
              </p>
              <p
                onClick={this.props.passDeal.bind(
                  null,
                  this.props.item.quote_id,
                  this.props.index
                )}
              >
                Pass
              </p>
            </div>
            <div
              className={
                this.props.isSnoozed === 1
                  ? "deal-options-right"
                  : "deal-options-right"
              }
            >
              <p
                onClick={this.props.snoozeDeal.bind(
                  null,
                  this.props.item.quote_id,
                  this.props.index,
                  this.props.individualIndex
                )}
                className={this.props.isSnoozed === 1 ? "red-text" : "red-text"}
              >
                <img
                  src="/images/icons/snooze-icon.png"
                  alt="snooze-icon"
                  className={
                    this.props.isSnoozed === 1 ? "deal-snooze-icon" : "hidden"
                  }
                />
                {this.props.isSnoozed === 1 ? "Snoozed" : "Snooze"}
              </p>
              <p
                onClick={this.props.irrelevantDeal.bind(
                  null,
                  this.props.item.quote_id,
                  this.props.index
                )}
                className={this.props.isSnoozed === 1 ? "" : "red-text"}
              >
                Irrelevant
              </p>
            </div>
          </Row>
        </Panel>
      </div>
    );
  }
}

export default individualBank;
