import React from "react";
import DealQuotes from "./dealQuotes";
import BankQuotes from "./bankQuotes";
import { Navigation } from "../../lib";
import Modal from "react-modal";
import Form from "muicss/lib/react/form";
import Button from "muicss/lib/react/button";
import moment from "moment";
import FormattedDatePicker from "../common/FormattedDatePicker";
import NoteModal from "../common/NoteModal";

class missingQuotesBrokerPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      passModalOpen: false,
      snoozeModalOpen: false,
      irrelevantModalOpen: false,
      snoozeDate: moment().format("MM/DD/YYYY"),
      quote_id: "",
      index: "",
      note: "",
      bank_id: "",
      deal_id: "",
    };
  }

  quoteDeal(quote_id, banker_id) {
    this.props.setPageAndScrollPos(window.location.pathname, window.scrollY);
    var path = encodeURIComponent(window.location.pathname);
    sessionStorage.setItem("mq_broker_active_banker_id", banker_id);
    Navigation.go(`/quote-form/1/${quote_id}/${path}`);
  }

  passDeal(quote_id, index) {
    this.setState({ passModalOpen: true, quote_id: quote_id, index: index });
  }

  snoozeDeal(quote_id, index, individualIndex) {
    this.setState({
      snoozeModalOpen: true,
      quote_id: quote_id,
      index: index,
      individualIndex: individualIndex,
    });
  }

  irrelevantDeal(quote_id, index) {
    this.setState({
      irrelevantModalOpen: true,
      quote_id: quote_id,
      index: index,
    });
  }

  submitPassDialog(e) {
    e.preventDefault();
    this.props.passDeal(this.state.quote_id, this.state.note, this.state.index);
    this.setState({ passModalOpen: false, quote_id: "", note: "", index: "" });
  }

  closeIrrelevantDialog(e) {
    e.preventDefault();
    this.setState({
      irrelevantModalOpen: false,
      quote_id: "",
      note: "",
      index: "",
    });
  }

  submitIrrelevantDialog(e) {
    e.preventDefault();
    this.props.irrelevantDeal(
      this.state.quote_id,
      this.state.note,
      this.state.index
    );
    this.setState({
      irrelevantModalOpen: false,
      quote_id: "",
      note: "",
      index: "",
    });
  }

  closePassDialog(e) {
    e.preventDefault();
    this.setState({ passModalOpen: false, quote_id: "", note: "", index: "" });
  }

  closeSnoozeDialog(e) {
    e.preventDefault();
    this.setState({
      snoozeModalOpen: false,
      quote_id: "",
      snoozeDate: "",
      index: "",
    });
  }

  submitSnoozeDialog(e) {
    e.preventDefault();
    this.props.snoozeDeal(
      this.state.quote_id,
      this.state.snoozeDate,
      this.state.index,
      this.state.individualIndex
    );
    this.setState({
      snoozeModalOpen: false,
      quote_id: "",
      snoozeDate: "",
      index: "",
      individualIndex: "",
    });
  }

  snoozeDateChange(date) {
    this.setState({ snoozeDate: date });
  }

  render() {
    let missingQuotes = null;
    if (!this.props.noQuotes) {
      if (this.props.toggle === "true" || this.props.toggle === true) {
        missingQuotes =
          this.props.quotes &&
          this.props.quotes.map((item, index) => (
            <BankQuotes
              key={index}
              index={index}
              deal={item}
              quoteDeal={this.quoteDeal.bind(this)}
              passDeal={this.passDeal.bind(this)}
              snoozeDeal={this.snoozeDeal.bind(this)}
              irrelevantDeal={this.irrelevantDeal.bind(this)}
              authentication={this.props.authentication}
              isSearchActive={this.props.isSearchActive}
              isSearchDeal={this.props.isSearchDeal}
              loadingAdminData={this.props.loadingAdminData}
              noAdminViewData={this.props.noAdminViewData}
              getAdminViewData={this.props.getAdminViewData}
              removeAdminViewData={this.props.removeAdminViewData}
              localAdminBankId={this.props.localAdminBankId}
              getContactFullInfo={this.props.getContactFullInfo}
              contact={this.props.contact}
              loadingContact={this.props.loadingContact}
              noContact={this.props.noContact}
              updateQTSStar={this.props.updateBankQTSStar}
              openDealDetails={this.props.openBankDD}
              routeToMissingQuotesByBroker={
                this.props.routeToMissingQuotesByBroker
              }
            />
          ));
      } else {
        missingQuotes =
          this.props.quotes &&
          this.props.quotes.map((item, index) => (
            <DealQuotes
              key={index}
              index={index}
              deal={item}
              quoteDeal={this.quoteDeal.bind(this)}
              passDeal={this.passDeal.bind(this)}
              snoozeDeal={this.snoozeDeal.bind(this)}
              irrelevantDeal={this.irrelevantDeal.bind(this)}
              getContactFullInfo={this.props.getContactFullInfo}
              contact={this.props.contact}
              loadingContact={this.props.loadingContact}
              noContact={this.props.noContact}
              openDealDetails={this.props.openDealDD}
              updateQTSStar={this.props.updateDealQTSStar}
              authentication={this.props.authentication}
              routeToMissingQuotesByBroker={
                this.props.routeToMissingQuotesByBroker
              }
            />
          ));
      }
    }
    return (
      <div className="mq-broker-container">
        <NoteModal
          isOpen={this.state.passModalOpen}
          onRequestClose={this.closePassDialog.bind(this)}
          onAfterOpen={() => {}}
          require={false}
          text={{ title: "Reason for passing (optional)", button: "PASS" }}
          onSubmit={this.submitPassDialog.bind(this)}
        />

        <NoteModal
          isOpen={this.state.irrelevantModalOpen}
          onRequestClose={this.closeIrrelevantDialog.bind(this)}
          onAfterOpen={() => {}}
          require={false}
          text={{
            title: "Reason for marking irrelevant (optional)",
            button: "IRRELEVANT",
          }}
          onSubmit={this.submitIrrelevantDialog.bind(this)}
        />

        <Modal
          className="note-modal snooze-modal"
          isOpen={this.state.snoozeModalOpen}
          ariaHideApp={false}
          contentLabel="Modal"
          onAfterOpen={function () {}}
          onRequestClose={this.closeSnoozeDialog.bind(this)}
          shouldCloseOnOverlayClick={true}
        >
          <Form className="modal-form">
            <p className="add-note-modal">Snooze Until</p>
            <FormattedDatePicker
              allowClear={false}
              dateValue={moment().format("YYYY-MM-DD")}
              update={this.snoozeDateChange.bind(this)}
            />
            <div className="add-note-buttons">
              <Button
                variant="flat"
                onClick={this.closeSnoozeDialog.bind(this)}
              >
                CANCEL
              </Button>
              <Button
                variant="flat"
                onClick={this.submitSnoozeDialog.bind(this)}
                type="submit"
                value="submit"
              >
                OK
              </Button>
            </div>
          </Form>
        </Modal>
        {missingQuotes}
        <div className={this.props.noQuotes ? "" : "hidden"}>
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">No quotes found</h2>
          </div>
        </div>
      </div>
    );
  }
}

export default missingQuotesBrokerPage;
