import React from "react";
import Row from "muicss/lib/react/row";
import Panel from "muicss/lib/react/panel";
import Col from "muicss/lib/react/col";
import Bank from "./individualBank";
import ClientModal from "../common/ClientModal";

class bankQuotes extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cardShowing: false,
      viewAllChecked: false,
      isSearchActive: "",
      isSearchDeal: false,
      modalOpen: false,
      contact: {},
      brokerName: "",
      brokerId: "",
    };
    this.openContactModal = this.openContactModal.bind(this);
  }

  componentDidMount() {
    if (
      sessionStorage.getItem("mq_broker_active_banker_id") ===
      this.props.deal.banker_id
    ) {
      this.setState({ cardShowing: true });
    }
  }

  UNSAFE_componentWillMount() {
    if (this.props.isSearchDeal) {
      this.setState({ viewAllChecked: true });
    } else {
      this.setState({ viewAllChecked: false });
    }
  }

  openChildDeals() {
    this.setState({ cardShowing: !this.state.cardShowing });
  }

  viewAllCheckbox() {
    this.setState({ viewAllChecked: !this.state.viewAllChecked }, function () {
      if (this.state.viewAllChecked) {
        this.props.getAdminViewData(
          this.props.deal.bank_id,
          this.props.deal.banker_id
        );
      } else {
        this.props.removeAdminViewData(this.props.deal.bank_id);
      }
    });
  }

  getBrokerInfo(item) {
    for (var i = 0; i < item.length; i++) {
      if (item[i].broker_of_record === 1 || item[i].is_originator === 1) {
        return item[i].broker_name;
      }
    }
  }

  getBrokerIdInfo(item) {
    for (var i = 0; i < item.length; i++) {
      if (item[i].broker_of_record === 1 || item[i].is_originator === 1) {
        return item[i].member_contact_id;
      }
    }
  }

  getBrokerIdForMQ(item) {
    for (var i = 0; i < item.length; i++) {
      if (item[i].broker_of_record === 1 || item[i].is_originator === 1) {
        return item[i].broker_id;
      }
    }
  }

  openContactModal(contactId, contactName) {
    this.props.getContactFullInfo(contactId);
    this.setState({
      modalOpen: true,
      brokerName: contactName,
      brokerId: contactId,
    });
  }

  closeModal() {
    this.setState({
      modalOpen: false,
    });
  }

  render() {
    const deals = this.props.deal.my_quotes.map((item, index) => (
      <Bank
        key={index}
        index={this.props.index}
        individualIndex={index}
        item={item}
        isSnoozed={item.missing_quotes_snooze}
        banker_id={this.props.deal.banker_id}
        bank_id={this.props.deal.bank_id}
        brokerIdForMQ={
          item.broker_data && this.getBrokerIdForMQ(item.broker_data)
        }
        broker={item.broker_data && this.getBrokerInfo(item.broker_data)}
        brokerId={item.broker_data && this.getBrokerIdInfo(item.broker_data)}
        quoteDeal={this.props.quoteDeal}
        passDeal={this.props.passDeal}
        snoozeDeal={this.props.snoozeDeal}
        irrelevantDeal={this.props.irrelevantDeal}
        getContactFullInfo={this.props.getContactFullInfo}
        openDealDetails={this.props.openDealDetails}
        openContactModal={this.openContactModal}
        updateQTSStar={this.props.updateQTSStar}
        authentication={this.props.authentication}
        routeToMissingQuotesByBroker={this.props.routeToMissingQuotesByBroker}
      />
    ));
    let other_deals = "";
    if (this.props.deal.other_quotes && this.props.deal.other_quotes.length) {
      other_deals = this.props.deal.other_quotes.map((item, index) => (
        <Bank
          key={index}
          item={item}
          individualIndex={index}
          isSnoozed={item.missing_quotes_snooze}
          index={this.props.index}
          banker_id={this.props.deal.banker_id}
          bank_id={this.props.deal.bank_id}
          broker={item.broker_data && this.getBrokerInfo(item.broker_data)}
          brokerId={item.broker_data && this.getBrokerIdInfo(item.broker_data)}
          brokerIdForMQ={
            item.broker_data && this.getBrokerIdForMQ(item.broker_data)
          }
          quoteDeal={this.props.quoteDeal}
          passDeal={this.props.passDeal}
          snoozeDeal={this.props.snoozeDeal}
          irrelevantDeal={this.props.irrelevantDeal}
          getContactFullInfo={this.props.getContactFullInfo}
          openDealDetails={this.props.openDealDetails}
          openContactModal={this.openContactModal}
          updateQTSStar={this.props.updateQTSStar}
          authentication={this.props.authentication}
          routeToMissingQuotesByBroker={this.props.routeToMissingQuotesByBroker}
        />
      ));
    }
    return (
      <div className="mq-broker-page">
        <ClientModal
          isOpen={this.state.modalOpen}
          onRequestClose={this.closeModal.bind(this)}
          onAfterOpen={() => ({})}
          name={this.state.brokerName}
          loading={this.props.loadingContact}
          contact={this.props.contact}
          noContact={this.props.noContact}
        />
        <Panel className="panel border-left-panel">
          <Row className="mq-broker-bank-row">
            <Col md="3" sm="4" xs="12">
              <p className="header-text">{this.props.deal.bank_name}</p>
              <p className="subtitle-text">Bank</p>
            </Col>
            <Col md="3" sm="4" xs="12">
              <p
                className="header-text orange-hover"
                onClick={this.openContactModal.bind(
                  null,
                  this.props.deal.banker_id,
                  this.props.deal.banker_name
                )}
              >
                {this.props.deal.banker_name}
              </p>
              <p className="subtitle-text">Banker</p>
            </Col>
            <Col md="6" sm="4" xs="12">
              {this.props.deal.bank_notes ? (
                <p className="header-text">{this.props.deal.bank_notes}</p>
              ) : (
                <p className="subtitle-text-dark inline-block">
                  <i>No recent notes</i>
                </p>
              )}
              <p className="subtitle-text">Most Recent Note</p>
            </Col>
          </Row>
          <div className="mq-deal-column-right">
            <div className={this.state.cardShowing ? "" : "hidden"}>
              <div
                className={
                  this.props.authentication.is_admin ||
                  this.props.authentication.is_qts
                    ? "mq-deal-column-left"
                    : "hidden"
                }
              >
                <div className="rkmd-checkbox checkbox-ripple">
                  <label className="input-checkbox checkbox-lightBlue">
                    <input
                      type="checkbox"
                      id="view-all"
                      checked={this.state.viewAllChecked}
                      onChange={this.viewAllCheckbox.bind(this)}
                    />
                    <span className="checkbox"></span>
                    <span className="ripple"></span>
                  </label>
                  <label htmlFor="view-all" className="label">
                    View All
                  </label>
                </div>
              </div>
            </div>
            <div className="mq-deal-column-right">
              <div className="mq-deal-amount">
                {this.props.deal.my_quotes.filter(i => i.quote_status !== 7).length}
              </div>
              <i
                className="material-icons arrow-down"
                onClick={this.openChildDeals.bind(this)}
              >
                {this.state.cardShowing
                  ? "keyboard_arrow_up"
                  : "keyboard_arrow_down"}
              </i>
            </div>
          </div>
        </Panel>
        <div
          className={this.state.cardShowing ? "hidden" : "after-panel"}
        ></div>
        <div className={this.state.cardShowing ? "" : "hidden"}>
          {deals}
          <div
            className={
              this.props.loadingAdminData &&
              this.props.localAdminBankId === this.props.deal.bank_id
                ? "mq-deal-loading orange-text"
                : "hidden"
            }
          >
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
          <div
            className={
              this.props.noAdminViewData &&
              !this.props.loadingAdminData &&
              this.props.localAdminBankId === this.props.deal.bank_id
                ? "no-mq-deals"
                : "hidden"
            }
          >
            No other deals for this bank
          </div>
          {other_deals}
        </div>
      </div>
    );
  }
}

export default bankQuotes;
