import * as types from "./actionTypes";
import config from "../config";
import { tokenExpired } from "./authenticationActions";
import { ajax } from "../lib";

export function loadMissingQuotesbyBank(sort_by) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_BROKER_QUOTES });
    ajax({
      url: `${config.BASE_URL}broker-missing`,
      applyAuthHeader: true,
      data: { sort_by },
      success: function (response) {
        if (!response.isResponse) {
          dispatch(tokenExpired());
        }
        if (response.status === "204") {
          dispatch({ type: types.NO_BROKER_QUOTES });
        } else {
          dispatch({
            type: types.LOAD_BROKER_MISSING_QUOTES_SUCCESS,
            data: response.data,
          });
        }
      },
    });
  };
}

