export function passDeal(quote_id, note, index) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}missing-quotes/pass-deal`,
      applyAuthHeader: true,
      type: "POST",
      data: { quote_id, note },
      success: function (response) {
        if (response.status === "E408") {
          dispatch({
            type: types.ERROR_UPDATING_MQ_BROKER,
            message: response.message,
          });
        }
        if (response.status === "E107") {
          dispatch({
            type: types.ERROR_UPDATING_MQ_BROKER,
            message: response.message,
          });
        }
        if (response.status === "E404") {
          dispatch(tokenExpired());
        }
        if (response.isResponse) {
          dispatch({
            type: types.PASS_MQ_BROKER_DEAL_SUCCESS,
            data: response.message,
            quote_id: quote_id,
            index: index,
          });
        }
      },
    });
  };
}

