export function search(search, sort_by) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_SEARCH_BROKER_QUOTES });
    ajax({
      url: `${config.BASE_URL}broker-missing-search`,
      applyAuthHeader: true,
      data: { search, sort_by },
      success: function (response) {
        if (response.status === "204") {
          dispatch({ type: types.NO_MQ_BROKER_SEARCH_RESULTS });
        }
        if (response.status === "E107") {
          dispatch({
            type: types.ERROR_UPDATING_MQ_BROKER,
            message: response.message,
          });
        }
        if (response.status === "E404") {
          dispatch(tokenExpired());
        }
        if (response.data && response.data.length) {
          dispatch({
            type: types.LOAD_SEARCH_MQ_BROKER_DATA,
            data: response.data,
          });
        }
      },
    });
  };
}

export function getFullContactInfo(contact_id) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_FULL_CONTACT });
    ajax({
      url: `${config.BASE_URL}contact/${contact_id}`,
      applyAuthHeader: true,
      success: function (response) {
        if (response.data && response.data.length >= 1) {
          dispatch({
            type: types.LOAD_FULL_CONTACT,
            contact: response.data[0].contacts[0],
          });
        } else {
          dispatch({ type: types.NO_FULL_CONTACT });
        }
      },
    });
  };
}

export function updateNote(quote_id, notes, index, banker_id) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_FULL_CONTACT });
    ajax({
      url: `${config.BASE_URL}save-quote`,
      applyAuthHeader: true,
      type: "POST",
      data: { notes, quote_id },
      success: function (response) {
        if (response.isResponse) {
          dispatch({
            type: types.UPDATE_MQ_NOTE,
            message: response.message,
            index: index,
            banker_id: banker_id,
            notes: notes,
          });
        } else {
          dispatch({
            type: types.ERROR_UPDATE_MQ_NOTE,
            message: response.message,
          });
        }
      },
    });
  };
}

