export function updateBankQTSStar(deal_id, banker_id, quote_id, starred) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_FULL_CONTACT });
    ajax({
      url: `${config.BASE_URL}star-qts`,
      applyAuthHeader: true,
      type: "POST",
      data: { deal_id, starred },
      success: function (response) {
        if (response.status === "206") {
          dispatch({
            type: types.SUCCESS_BANK_UPDATE_MQ_QTS_STAR,
            message: response.message,
            banker_id: banker_id,
            quote_id: quote_id,
            starred: starred,
          });
        }
        if (response.status === "E404") {
          dispatch(tokenExpired());
        }
        if (response.status === "E107" || response.status === "E408") {
          dispatch({
            type: types.ERROR_UPDATE_MQ_QTS_STAR,
            message: response.message,
          });
        }
      },
    });
  };
}

